body {
    background-color: #282c34;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;

    font-size: 2vmin;
    color: white;
}

.App-link {
    color: #61dafb;
}

.App {
}

nav {
    background-color: #61dafb;
    margin: 5px;
}

nav a {
    padding: 5px;
    text-decoration: none;
}

nav a.active {
    font-weight: bold;
}

img {
    align-self: center;
}

.App-header button {
    background-color: #61dafb;
    border-radius: 10px;

    margin: 0px 10px;
}

.App-header ul {
    display: flex;
    padding: 0px;
    margin: 0px;
    height: 80px;
    justify-content: flex-end;
    align-items: center;
    flex-grow: 1;
    border-bottom: 1px solid gray;
}

img {
    height: 30px;
}

.App-header li {
    list-style: none;
    padding: auto;

    height: 30px;
    margin: 0px 10px;
    font-size: 15 px;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.bodyContainer {
    display: flex;
    height: 100vh;
}
.menu {
    background-color: darkgray;

    width: 20%;
}

.menu .sideMenu > li {
    list-style: none;
    cursor: pointer;
    padding: 10px;
}

.menu .sideMenu > li:hover {
    background-color: gray;
}

.menu .sideMenu {
    padding: 0 !important;
    margin: 0 !important;
}

.menu .sideMenu > li ul {
    display: none;
    padding: 0;
    margin: 0;
}

.menu .sideMenu > li ul li {
    list-style: none;
    cursor: pointer;
    padding: 10px;
    background-color: darkgrey;
}

.menu .sideMenu > li:hover ul {
    display: list-item;
}

.details {
    width: 80%;
}

.homeMain {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.homeContent {
    background-color: honeydew;
    width: 300px;
    height: 310px;
    margin: 0px;
}

.gameOver {
    background-color: honeydew;
    width: 300px;
    height: 310px;
    margin: 0px;
    opacity: 50%;
}

.homeCard {
    width: 98px;
    height: 100px;
    margin: 0px;
    padding: 0px;
    display: inline-block;
    border: 1px solid darkgrey;
}

.circle {
    border-radius: 50%;
    border: 3px solid black;
    align-content: center;
    width: 80px;
    height: 80px;
    margin: 8px;
}

.circle3::before {
    content: "";
    margin: 5px;
    text-align: center;
    display: block;
    width: 80px;
    height: 80px;
    border-radius: 50px;
    /* background-color: darkgrey; */
    border: 3px solid black;
}

.cross {
    position: relative;
}

.cross:after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: "\274c"; /* use the hex value here... */
    font-size: 60px;
    color: darkgrey;
    line-height: 100px;
    text-align: center;
}

.won {
    position: absolute;
    font-size: 30px;
    background-color: darkseagreen;
    padding: 5px;
}
